import React, { useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { Navbar, Button } from 'react-bootstrap';
import './HeaderBar.css';
import { AuthContext } from '../AuthContext';

const HeaderBar = () => {
  // AuthContext   
  const { logout } = useContext(AuthContext);
  //    navigate  
  const navigate = useNavigate();

  return (
    <>
      <Navbar bg="light" expand="sm" className="justify-content-between custom-navbar">
        {/*     */}
        <Navbar.Brand href="/dashboard">
          <img src='/logo_real.png' alt="Logo" />
           
        </Navbar.Brand>
        {/*   */}
        <Button style={{margin: "10px"}} onClick={() => {logout(); navigate('/');}}>
          <span className="material-symbols-outlined" style={{verticalAlign: "middle"}}>logout</span>
          <span style={{verticalAlign: "middle", fontWeight: "bold"}}> </span>
          
        </Button>
      </Navbar>
    </>
  );
};

export default HeaderBar;