import React, { useState, useContext } from 'react';
import { AuthContext } from '../../AuthContext';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { Container, Row, Col, Card, Form, Button } from 'react-bootstrap';

function LoginPage() {
  //   
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  
  //    useNavigate    
  const navigate = useNavigate();
  const { login } = useContext(AuthContext);

  //      
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    try {
      //    
      const response = await axios.post(
        '/admin/login',
        { username, password },
        { withCredentials: true }
      );

      if (response.data.success) {
        //      
        console.log(response.config.data.split(","));
        const user = JSON.parse(response.config.data)
        login(user);
        navigate('/dashboard');
        console.log('success~~');
      } /* else if (response.data.success === false) {
        //      
        alert(response.data.message, "status: ", response.data.status);
      } */
    } catch (error) {
      //      
      console.error('Login failed:', error);
      alert('Login failed: ' + JSON.stringify(error.response.data.message));
    }
  };

  return (
    <Container className="mt-5">
      <Row className="justify-content-center">
        <Col md={6}>
          <Card>
            <Card.Header>
              <h4> </h4>
            </Card.Header>
            <Card.Body>
              <div className="text-center mb-4">
                <img src="/logo_real.png" alt="Logo" className="logo" />
              </div>
              <Form onSubmit={handleSubmit}>
              <div >
                  <div>
                    <Form.Group controlId="username">
                      <Form.Control
                        type="text"
                        placeholder="  "
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                      />
                    </Form.Group>
                    <Form.Group controlId="password" className="mt-3">
                      <Form.Control
                        type="password"
                        placeholder="  "
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                      />
                    </Form.Group>
                  </div>
                </div>
                <p></p>
                <Button variant="primary" type="submit" className="ml-2" style={{ height: '150%', width: '100%' }}>
                  <div style={{fontWeight: "bold"}}></div>
                </Button>
              </Form>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
}

export default LoginPage;